.Grid.PageHeader__Detail.slds-page-header__detail-row.slds-grid {
  padding: 1rem 1rem;
  border-bottom: 1px solid #dddbda;
  border-radius: 0.25rem;
  background: #f3f2f2;
  background-clip: padding-box;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #dddbda;
}

.headerFormElement {
  margin-left: 8px !important;
  //max-width: 300px;
}

.text_Grid {
  margin-top: 8px !important;
  margin-right: 10px !important;
}

.headeer_Text {
  margin-top: 8px !important;
}

.center_Grid {
  display: flex !important;
}

.last_Grid {
  display: contents !important;
}

.slds-button_neutral.slds-is-selected {
  background-color: #0070d2 !important;
  color: white !important;
}

.IBHistory_Btn_svg {
  fill: #0070d2 !important;
}

.IBHistory_Btn_svg_selected {
  fill: #ffffff !important;
}

.HeaderTypeModal__svg {
  background-color: #004f9e !important;
  fill: #ffffff !important;
}

.Header_linkButton {
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  display: inline;
  margin: 0;
  padding: 0;
}

.Header_linkButton:hover,
.Header_linkButton:focus {
  text-decoration: none;
}
