.IBDataGridDiv {
  display: flex;
  //max-width: 700px;
  margin-top: 10px;
}
.IBDataGrid_Grid {
  width: 100%;
}

.IBDataGrid_Grid_Image {
  background-color: '#FF0000';
}

.MappingTypeModal__svg {
  fill: '#0070d2' !important;
}

.IBFilterLabel {
  width: 100%;
  text-align: center;
  //font-size: large;
  //font-weight: bold;
}

.input-color {
  position: relative;
}
.input-color label {
  padding-left: 20px;
}
.input-color .color-box {
  width: 10px;
  height: 10px;
  display: inline-block;
  background-color: #ccc;
  position: absolute;
  left: 5px;
  top: 5px;
  background-color: #ff0000;
}
.input-color .color-box-min {
  width: 10px;
  height: 10px;
  display: inline-block;
  background-color: #ccc;
  position: absolute;
  left: 5px;
  top: 5px;
  background-color: #ff8c00;
}

.legend-div {
  display: flex;
  position: relative;
  float: right;
}
